}
.particles{
    background: #000000;
    display: flex;
    vertical-align: bottom;
    width: 100%;
    min-height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.header{
    display: flex;
    flex-direction: column;
    z-index: 1;
    text-align: center;
    color: #fff;
    position: absolute;
    top:50%;
    left: 50%;
    margin:auto;
    transform: translate(-50%, -50%);
  
}
.header h1{
    font-size:120px;
    margin:0px;
    line-height: 100px;
}
.header h2{
    margin:20px 0 0 0 ; 
    font-weight: bold !important;
    font-size: 70px;
}
@media screen and (max-width:700px){
    .header{
        width:100%;
    }
