.pagelinks{
    margin-top:40px;
}
.pagenav{
    color:#fff;
    padding:10px;
    font-size: 18px;
    text-decoration: none;
    margin:5px;
    border:1px solid #fff;
    border-radius: 20px;
    transition-duration:.5s;
}
.pagenav:hover{
    color:#000;
    background: #fff;
}
.down{
    color:#fff;
    position: absolute;
    bottom: 10px;
    width: 100%;
    margin: 0 auto;
    display: block;
    font-size: 30px;
    cursor: pointer;
    text-align: center;
  
    height: 100px;
    fill: #fff;
  
    animation: pulse 1.3s infinite;

}
@keyframes pulse{
    0%,100%{
        opacity: .5;
        bottom: -30px;
    }
    50%{
        filter: none;
        opacity: 1;
        bottom: -20px;
    }
}
.down>svg{
    position:absolute;
    transform-style: preserve-3d;
    transform: translate(-50%,-50%);
    animation: pulse 1.3s infinite;
    width:100px;
    height: 100px;
}
.down:hover{
    color:#000;
    
}
